@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5 !important;
    -webkit-font-smothing: antialiased;
}

.logon-container{
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logon-container div.form{
    width: 100%;   
    min-height: 300px;
    background-color: #FFF; 
    margin-right: 30px;
    border-radius: 5px;

    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
    ;
              
    margin: 100px auto;
    background: white;
    border-radius: 5px;
}

.logo{
    margin-top: 10px;
    margin-left: 10px;
    font-size: 32px;
    color:  rgb(236, 44, 44);;
    padding: 0px 8px !important;    
    width: 155px;
    border-width: 10px;
    border-style: solid;
    border-color: rgb(236, 44, 44);
    font-weight: bolder;
}

.col{
    text-align: center;
    padding: 10px;
    padding-top: 20px;
}

.button{
    margin-top: 5px;  
}

.text{
    margin-top: 20px;
    padding: 10px !important;
}

.footer{
    float: right;
    padding: 10px;
}

